#global-header-bar,
#content {
  max-width: inherit;
  margin: 0 30px;
}

#global-header .header-wrapper {
  max-width: inherit;
}

// HEADER
@media (min-width: 769px) {
  #global-header.with-proposition .header-wrapper .header-proposition {
    width: 50.00%;
    float: left;
  }

  #global-header.with-proposition .header-wrapper .header-profile {
    width: 16.66%;
    float: left;
    
    p {
      color: #fff;
      margin-right: 15px;
      float: right;
    }
    a {
      color: #fff;
    }

  }
}

// Additional layouts
.column-three-quarters {
  @include grid-column(3 / 4);
}

.column-one-fifth {
  @include grid-column(1 / 5);
}

.column-three-fifths {
  @include grid-column(3 / 5);
}

.column-four-fifths {
  @include grid-column(4 / 5);
}


.edit {
  position: relative;

  .edit-link {
    text-align: right;
    position: absolute;
    top: 3px;
    right: 0;
  }
}