.button-secondary{
  @include button($grey-3);
}

.button-search {
  @include button($govuk-blue);
}

.button-link{
  @include button(transparent);
  box-shadow: none;
  color: $link-colour;
  &:active {
    top: 0px;
    
  }
  &:hover {
    text-decoration: $link-hover-colour;
    background-color: transparent;
    border-color: transparent;
  }
  
  &:link,
  &:link:focus,
  &:hover,
  &:focus,
  &:visited {
    color: $link-colour;
  }
}
