.details {
  > * {
    position: relative;
    border-bottom: 1px solid $border-colour;

    > * {
      display: block;
    }
  }

  .details-warning {
    color: $error-colour;
  }

  .details-term {
    font-weight: bold;
    margin: em(12, 19) 4em em(4,19) 0;
  }

  > *:first-child .details-term {
    margin-top: 0;
  }

  .details-definition {
    padding-bottom: em(9, 19);
    overflow-wrap: break-word;
  }
}

.details-verified {
  text-align: right;
  position: absolute;
  top: 0;
  right: 0;
}

.notice-heading {
  margin-bottom: $gutter;
}